import React, { Component } from 'react';
import firebase from './firebase';
import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      features: []
    }
  }

  componentDidMount() { 
    const dbRef = firebase.database().ref();
    dbRef.on('value', (snapshot) => {
      const data = snapshot.val();
      console.log( data );
      
      const newFeaturesArray = []; 

      for (let propertyName in data) {
        newFeaturesArray.push( data[propertyName] )
      }
      console.log(newFeaturesArray)

      this.setState({
        features: newFeaturesArray
      })
    })
  }

  render () {
    return (
      <div className="App">
        <h1>A Sustainable Home</h1>

        <ul>
        {
          // 2. use .map() to return data so we can see info on the page (similar to append)
          this.state.features.map( (features) => {
            return <li> {features} </li>
          })
        }
        </ul>
      </div>
    );
  }
}

export default App;
